<template>
  <div class="todoProcess">
    <el-container>
        <el-header>
          <div class="top-header">
            <span class="top-breadCrumb">流程报表</span>
          </div>
        </el-header>
      <el-main>
        <div class="mainContaint minWidth">
          <div class="list">
          <div v-bind:style="{'height':fullHeight - 110 + 'px'}" style="overflow:auto;padding:0 25px">
            <div class="menuList" >
                <el-row>
                  <el-col :span="24" class="mb24">
                    <h2>静态报表</h2>
                  </el-col>
                </el-row>
              <el-row class=" flowForm">
                <el-col :span="4" class="mb78">流程设置分析</el-col>
                  <el-col :span="20">
                     <ul class="staticFrom cameo">
                         <li @click="burialPointEvent('流程报表-统计有效流程/模板数量')">
                           <router-link to="/reports/statisticsMiddle">
                           <span>统计有效流程/模板数量</span></router-link>
                         </li>
                         <li @click="burialPointEvent('流程报表-静态-统计某个人员参与的流程报表')">
                           <router-link to="/reports/statisticsJob">
                           <span>静态-统计某个人员参与的流程报表</span></router-link>
                         </li>
                         <li @click="burialPointEvent('流程报表-流程静态配置导出')">
                           <router-link to="/reports/staticReports">
                           <span>流程静态配置导出</span></router-link>
                         </li>
                     </ul>
                  </el-col>
                </el-row>
            </div>
              <div class="menuList">
                <el-row class="mb24">
                  <el-col :span="24" >
                    <h2>动态报表 </h2>
                  </el-col>
                </el-row>
                    <div class="flowForm">
                      <el-row>
                        <el-col :span="4">  <span>流程使用频率分析 </span></el-col>
                        <el-col :span="20">
                          <ul class="staticFrom cameo">
                          <li @click="burialPointEvent('流程使用量统计')"><router-link to="/reports/peopleJob"><span>流程使用量统计</span></router-link></li>
                        <li @click="burialPointEvent('流程报表-以“步骤名称”统计实例最多的流程')"><router-link to="/reports/reportsInstance"><span>以“步骤名称”统计实例最多的流程</span></router-link></li>
                        <li @click="burialPointEvent('流程报表-以“人”统计实例最多的流程')"> <router-link to="/reports/peoplePublich"><span>以“人”统计实例最多的流程</span></router-link> </li>
                          </ul>
                        </el-col>
                      </el-row>
                      <el-row >
                        <el-col :span="4">
                          <span>流程时效分析 </span>
                        </el-col>
                        <el-col :span="20">
                          <ul class="staticFrom cameo">
                          <li @click="burialPointEvent('流程报表-耗时最长的流程名')"> <router-link to="/reports/prescriptionReports"><span>耗时最长的流程名</span></router-link></li>
                          <li @click="burialPointEvent('流程报表-耗时最长的实例')">   <router-link to="/reports/reportsTheLongest"><span>耗时最长的实例</span></router-link> </li>
                          </ul>
                        </el-col>
                      </el-row>
                      <el-row >
                        <el-col :span="4">
                          <span>流程质量分析</span>
                        </el-col>
                        <el-col :span="20" >
                          <ul class="staticFrom cameo">
                            <li @click="burialPointEvent('流程报表-流程退回率统计')"><router-link to="/reports/reportsFallback"><span>流程退回率统计</span></router-link></li>
                            <li @click="burialPointEvent('流程报表-以“步骤名称”统计退回率')"><router-link to="/reports/statisticalRate"><span>以“步骤名称”统计退回率</span> </router-link></li>
                            <li @click="burialPointEvent('流程报表-以“人”统计退回率')"><router-link to="/reports/reportsPeopleBack"><span>以“人”统计退回率</span></router-link></li>
                            <li @click="burialPointEvent('流程报表-审批意见的统计，以人为单位统计')"><router-link to="/reports/opinionReports"><span>审批意见的统计，以人为单位统计</span></router-link> </li>
                          </ul>
                        </el-col>
                    </el-row>
                      <el-row>
                        <el-col :span="4">
                          <span>历史报表分析</span>
                        </el-col>
                        <el-col :span="20">
                          <ul class="staticFrom cameo">
                            <li @click="burialPointEvent('流程报表-流程实例基础数据表')"><router-link to="/reports/processInstance"><span>流程实例基础数据表</span></router-link> </li>
                            <li @click="burialPointEvent('流程报表-审批操作基础数据表')"><router-link to="/reports/approvalForm"><span>审批操作基础数据表</span></router-link> </li>
                            <li @click="burialPointEvent('流程报表-用户平均审批时间')"><router-link to="/reports/approvalTime"><span>用户平均审批时间</span></router-link> </li>
                            <li @click="burialPointEvent('流程报表-未结束流程列表')"><router-link to="/reports/notFinished"><span>未结束流程列表</span></router-link> </li>
                          </ul>
                        </el-col>
                      </el-row>
                  </div>
              </div>
        </div>
        </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>  
  import util from "../../js/util";
  export default {
    name: 'reportsProcess',
    data() {
      return {
        fullHeight: document.documentElement.clientHeight,
        activeNames: ['1']
      }
    },
    methods:{
      /**
      * 埋点事件
      */
      burialPointEvent(event){
        util.burialPoint(event);
      },
      handleChange(val) {
      }
    },
    watch: {
      fullHeight () {
        return document.documentElement.clientHeight;
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  a {
    text-decoration: none;
  }
  .mb24{
    margin-bottom: 25px;
  }
  .mb78{
    margin-bottom: 60px;
  }

  .el-main {
    text-align: left;
    margin: 0;
    padding: 0;
       .minWidth{
          // min-width: 1000px;
        }
      .mainContaint {
        background: #ffffff;
        padding-top: 23px;
        padding-bottom: 0;/*新增加的代码*/
      }
    .list {
      background: #fff;
      border-radius: 5px;
      h2{
        font-size:15px;
      }
    }
    .menuList{
      .staticFrom{
        padding-left: 30px;
        li{
          display: inline-block;
          float: left;
          margin-right: 30px;
          margin-bottom: 25px;
        }
      }
      .cameo{
        li {
          a {
            font-size: 13px;
            color: #8f969b;
          }
          a:hover{
            color: #1fa0fd;
          }
        }
      }
    }
  }
</style>



